footer { 
background-color: $darkteal;
color: $white;
padding: 30px 0 0;
position: relative;
font-weight: 300;

.footer-row { 
display:flex;
flex-wrap:wrap;
justify-content: space-between;

    .footer-company-info
    {

      font-size: 1rem;
      width: 400px;
      flex: 100%;

       @media (min-width: $small) {
        
            flex: 50%;
            }

     @media (min-width: $medium) {
     
            flex: 25%;
            }

          
          
          



      
      .logo {
        margin-bottom:30px;
              width: 320px;

        img {
          margin-right: auto;
          display:inline-block;
          width:320px;
          height: 65px;
          vertical-align: middle;
        }
      }
        }


    .footer-menu {
        flex: 100%;
        margin-bottom: 30px;
        
        

        @media (min-width: $small) {
          flex: 50%;
            
            }

         @media (min-width: $medium) {
           flex: 25%;
            
            }

          @media (min-width: $large) {
            flex: 10%;
           
            margin-bottom: 40px;
          
            }

          h4 {
            text-transform: uppercase;
            font-size: 1rem;
            margin-bottom: 26px; 
          }
          ul {
            list-style: none;
            display:block;
              padding: 0;
              list-style-type: none;
           
          }
          li {
            font-size: .8125rem;
            line-height: 1.375rem;
          }
          a {
            text-decoration: none;
            color: $white;

            & :hover {
              text-decoration:underline;
            }
          }
    }
    }
  }      

  

